import { db } from '../firebase'
import { collection,getDocs } from 'firebase/firestore'
import { useEffect,useState } from 'react'
import Post from './Post'
import { useNavigate } from 'react-router-dom'

function EventList()
{
    const [eventos,setEventos] = useState([])
    const navegar = useNavigate()
    const [carregando,setCarregando] = useState(false)

    useEffect(() =>
    {
        async function fetchEventos()
        {
            setCarregando(true)
            try {
                const querySnepShot = await getDocs(collection(db, "eventos"))
                const eventosLista = querySnepShot.docs.map((doc) => ({
                    id: doc.id,
                    ...doc.data()
                }))

                setEventos(eventosLista)
            } catch (error) {
                console.log("Erro ao buscar eventos ", error)
            } finally {
                setCarregando(false)
            }
        }

        fetchEventos()
    }, [])

    return(
        <div>          
            <header className = "cabecalho">
                <h2>Eventos de doação</h2>
                <button onClick = {() => navegar('/criarevento')} >criar evento</button>
            </header>
            <br />

            {carregando ? ( <div style = {{height: 100+"%", display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center" }}>carregando os eventos...</div> ) : (
                eventos.map((evento) => (
                    <Post k = {evento.id} titulo = {evento.nome} descricao = {evento.descricao} meta={(evento.meta).toLocaleString('fr-FR')} valorArrecadado={evento.valor_arrecadado}/>
                ))
            )}
        </div>
    )
}

export default EventList